@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 400;
  src: local('Open Sans Regular'), local('OpenSans-Regular'), url(https://fonts.gstatic.com/s/opensans/v15/mem8YaGs126MiZpBA-UFUZ0e.ttf) format('truetype');
}
@font-face {
  font-family: 'Open Sans';
  font-style: normal;
  font-weight: 600;
  src: local('Open Sans SemiBold'), local('OpenSans-SemiBold'), url(https://fonts.gstatic.com/s/opensans/v15/mem5YaGs126MiZpBA-UNirkOVuhs.ttf) format('truetype');
}
html {
  height: 100%;
  font-size: 20px;
}
@media (max-width: 992px) {
  html {
    font-size: 18px;
  }
}
@media (max-width: 768px) {
  html {
    font-size: 16px;
  }
}
@media (max-width: 576px) {
  html {
    font-size: 14px;
  }
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 1rem;
  font: inherit;
  box-sizing: border-box;
}
input:hover {
  border: none;
  outline: none;
}
input:active {
  border: none;
  outline: none;
}
input:focus {
  border: none;
  outline: none;
}
ol,
ul {
  list-style: none;
}
a,
a:hover,
a:active {
  text-decoration: none;
  color: inherit;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
@media (min-width: 992px) {
  .container {
    max-width: 960px;
  }
}
body {
  font-family: "Open Sans";
  color: rgba(0, 0, 0, 0.8);
  height: 100%;
  display: flex;
  flex-direction: column;
}
section,
.container {
  display: flex;
  flex-direction: column;
}
main {
  flex: 1 0 auto;
}
body > header,
body > footer {
  display: flex;
  flex-direction: column;
  flex: 0 0 auto;
  padding: 1.25rem 0;
}
body > header {
  box-shadow: 0 3px 11px -6px rgba(0, 0, 0, 0.25);
}
.navbar {
  justify-content: space-between;
  padding: 0;
}
.navbar .logo {
  padding: 5px 0;
}
.navbar .navbar-nav {
  flex-grow: 0;
}
.navbar .navbar-nav .button {
  padding-left: 1.8rem;
  padding-right: 1.8rem;
}
.navbar:not([class*="expand"]) .navbar-nav {
  flex-direction: row;
}
.navbar:not([class*="expand"]) .navbar-nav a {
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}
@media (min-width: 992px) {
  .navbar:not([class*="expand"]) .navbar-nav a {
    margin-left: 1.25rem;
    margin-right: 1.25rem;
  }
}
.navbar:not([class*="expand"]) .navbar-nav a:last-of-type {
  margin-right: 0;
}
@media (min-width: 992px) {
  .navbar.navbar-expand-lg .navbar-nav a {
    margin-left: 1.25rem;
    margin-right: 1.25rem;
  }
  .navbar.navbar-expand-lg .navbar-nav a:last-of-type {
    margin-right: 0;
  }
}
section {
  margin: 4.25rem 0 1.25rem;
}
h1,
h2 {
  align-self: center;
  text-align: center;
  margin-bottom: 1.25rem;
}
@media (min-width: 992px) {
  h1,
  h2 {
    max-width: 66.7%;
  }
}
h1 {
  color: #232323;
  font-weight: 600;
  font-size: 2.4rem;
}
h2 {
  color: rgba(0, 0, 0, 0.8);
  font-weight: 600;
  font-size: 2rem;
}
h3 {
  color: rgba(0, 0, 0, 0.8);
  font-size: 1.2rem;
  font-weight: 600;
}
.button {
  font-size: 1rem;
  border-radius: 5px;
  background-color: #0089f9;
  color: white;
  padding: 0.5rem 1.25rem;
  cursor: pointer;
  text-align: center;
  align-self: center;
}
.button:hover {
  color: white;
}
.link {
  color: #4a90e2;
  text-decoration: underline;
}
.link:hover {
  color: #4a90e2;
  text-decoration: underline;
}
.correction {
  text-decoration: underline wavy #e12229;
  text-underline-position: under;
}
.texts h3 {
  margin: 10px 0 1.25rem 1.25rem;
}
.texts .date {
  padding-left: 1.25rem;
  margin-bottom: 0.75rem;
  font-size: 0.9rem;
}
.texts .text {
  padding: 1.25rem;
  box-shadow: 0 3px 13px rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  margin-bottom: 1.5rem;
}
.texts .owner {
  padding-left: 1.25rem;
  margin-bottom: 0.75rem;
}
.texts .note {
  border-radius: 5px;
  margin-bottom: 1.5rem;
  background-color: #f9fdff;
  padding: 1.25rem;
  height: 6rem;
  overflow: hidden;
  text-overflow: ellipsis;
}
.texts .editor {
  margin-left: auto;
}
.editor {
  display: flex;
}
.editor .photo {
  border-radius: 50%;
  margin-right: 1rem;
}
.editor .name {
  color: #0089f9;
  font-size: 1.2em;
  font-weight: 600;
}
.editor .description {
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.9em;
}
article.text {
  display: flex;
  flex-direction: column;
  font-size: 12px;
  padding: 1rem;
  padding-right: 1.4rem;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}
article.text header {
  display: flex;
  justify-content: space-between;
  padding-bottom: 10px;
}
article.text .body {
  font-size: 1rem;
  padding-right: 1rem;
}
article.text footer {
  padding-top: 5px;
  display: flex;
  justify-content: flex-end;
}
#when .reasons div {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
#when .reasons .icon {
  width: 60px;
  height: 60px;
  background-position: center;
  background-repeat: no-repeat;
}
