}
#faq {
  padding: 35px 0 55px;
  background-color: #f9fdff;
}
#faq .qa:last-of-type {
  margin-bottom: 30px;
}
#faq .qa h3:before {
  content: "Q: ";
}
#faq .qa h3:after {
  content: "?";
}
form {
  display: flex;
  flex-direction: column;
  align-self: center;
  padding: 0 20px;
}
form input {
  background-color: inherit;
  color: inherit;
  text-align: center;
  cursor: inherit;
}
form textarea {
  resize: none;
}
form .button {
  align-self: stretch;
}
legend {
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.9rem;
}
.legend {
  display: flex;
  align-items: center;
  justify-content: center;
}
.legend::before,
.legend::after {
  content: '';
  border: 2px solid rgba(216, 216, 216, 0.4);
  flex: 1 0 auto;
}
.legend legend {
  flex: 0 1 0;
  padding: 0 10px;
}
.input {
  position: relative;
  display: flex;
  padding: 0;
  border-radius: 5px;
  border: 1px solid transparent;
}
.input label {
  order: -1;
  margin: 0;
  border-right: 2px solid rgba(255, 255, 255, 0.2);
  width: 18px;
  height: 18px;
  padding: 15px;
  box-sizing: content-box;
}
