.input input {
  flex: 1 0 auto;
  padding: 0 15px;
}
.input .toggle {
  width: 30px;
  height: 18px;
  background: url(../img/visibility.png) no-repeat center;
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translate(0, -50%);
  cursor: pointer;
  opacity: 0.1;
}
.input:not(.button) {
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.input:not(.button) label {
  border-right: 1px solid rgba(151, 151, 151, 0.1);
}
.input:not(.button) input {
  text-align: left;
}
.input:not(.button) input::placeholder {
  color: rgba(0, 0, 0, 0.2);
  font-size: 0.8rem;
}
.input.password input {
  padding-right: 55px;
}
.forgot {
  margin: 20px 0;
  color: rgba(0, 0, 0, 0.5);
  font-size: 0.9rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.forgot * {
  flex: 1 1 auto;
}
.remark {
  color: #666666;
  font-size: 0.8rem;
}
.terms label {
  color: rgba(0, 0, 0, 0.2);
  font-size: 0.8rem;
  padding-left: 15px;
  margin-bottom: 30px;
}
fieldset {
  display: flex;
  flex-direction: column;
}
fieldset > * {
  margin-bottom: 20px;
}
[type="checkbox"] {
  display: none;
  padding: 0 1.6rem;
}
[type="checkbox"] + label::before {
  display: inline-block;
  content: '';
  width: 18px;
  height: 18px;
  border-radius: 2px;
  margin-right: 30px;
}
[type="checkbox"]:checked + label::before {
  border: 1px solid #0089f9;
  background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAA0AAAAKCAYAAABv7tTEAAAAoElEQVQoU2NkIBZ0/TJg+P9/PQMjYyAjUXogGvYD1QowMPw/SFgTigaGiwxsbA74NXX+DACaPh9iA0QDQyHjB4im9u8ODJWcB1Cc2vUrAegkkAYQgGsAcRgZYJL/GRYwVLAngpWgaPh/kIGNPQBkA8xQhCaQCEgjE+NBJBsWMpSzJ6AHFsR5qE6BqcGqAeI8GEDViFMDqiaEjQ7YnITsRADARUqe4Z3BOAAAAABJRU5ErkJggg==) no-repeat center;
}
[type="checkbox"]:not(:checked) + label::before {
