[type="checkbox"]:first-of-type {
  padding-left: 0;
}
[type="checkbox"]:last-of-type {
  padding-right: 0;
}
.plans {
  margin: 0;
}
@media (min-width: 992px) {
  .plans > * {
    padding: 0 0.8rem;
  }
  .plans > *:first-of-type {
    padding-left: 0;
    padding-right: 2.4rem;
  }
  .plans > *:last-of-type {
    padding-right: 0;
    padding-left: 2.4rem;
  }
}
.plan {
  display: flex;
  flex-direction: column;
  padding: 1.5rem;
  padding-bottom: 35px;
  box-shadow: 0 3px 13px rgba(0, 0, 0, 0.05);
  color: rgba(0, 0, 0, 0.8);
}
.plan > * {
  text-align: center;
  margin-bottom: 10px;
}
.plan h2 {
  font-size: 1rem;
  color: rgba(0, 0, 0, 0.4);
}
.plan .price {
  font-size: 3rem;
  font-weight: 600;
}
.plan .offer {
  font-size: 1.2rem;
  font-weight: 600;
}
.plan .button {
  margin-top: 10px;
  width: 181px;
}
.plan.active {
  background-color: #0089f9;
}
.plan.active > * {
  color: white;
}
.plan.active .button {
  background-color: white;
