  position: relative;
  align-self: flex-end;
  color: rgba(0, 0, 0, 0.2);
  font-size: 0.9rem;
}
.create-text .text {
  min-height: 11rem;
  padding: 12px 15px;
  margin-bottom: 30px;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.create-text .text::placeholder {
  color: rgba(0, 0, 0, 0.2);
  font-size: 0.9rem;
}
.create-text h2 {
  font-size: 1rem;
  font-weight: 400;
  text-align: left;
  align-self: flex-start;
  margin-bottom: 15px;
}
.create-text .comment {
  min-height: 5rem;
  padding: 12px 15px;
  margin-bottom: 20px;
  border-radius: 5px;
  border: 1px solid rgba(26, 24, 142, 0.1);
  background-color: #f9fdff;
}
.create-text .comment::placeholder {
  color: rgba(0, 0, 0, 0.6);
  font-size: 0.8rem;
}
.create-text .button {
