.modal-content {
  padding: 40px 25px 25px;
  border: none;
  align-items: center;
}
.modal-content h3 {
  text-align: center;
}
.modal-content .img {
  padding: 21px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
  border-radius: 7px;
  margin-bottom: 12px;
}
.modal-content .close {
  position: absolute;
  top: 25px;
  right: 25px;
  line-height: 100%;
  width: 1em;
}
